import { ArticleNode, ArticlePreviewInfo, BioNode, GlossaryTerm, TeamMemberInfo, TermNode } from "../types";
import { InternalLinks } from "../utils/links";


export const mapArticleNodeToArticlePreviewInfo = (source: ArticleNode) : ArticlePreviewInfo => ({
    previewImage: source.frontmatter?.previewImage ?? null,
    title: source.frontmatter?.title ?? "",
    date: source.frontmatter?.date ?? "",
    articleId: source.id,
    type: source.frontmatter?.articleType ?? "",
    url: source.fields?.slug ? `/news/${source.fields.slug}` : "",
    sortByUpdatedDate: source.frontmatter?.sortByUpdatedDate ?? false,
    lastUpdatedOn: source.frontmatter?.lastUpdatedOn ?? ""
})

export const mapBioNodeToTeamMemberInfo= (source: BioNode): TeamMemberInfo => ({
    name: `${source.frontmatter?.firstName} ${source.frontmatter?.lastName}`,
    image: source.frontmatter?.photo!,
    jobTitle: source.frontmatter?.position as string,
    url: source.fields?.slug ? `${InternalLinks.Bios}${source.fields?.slug}` : ""
})

export const mapTermNodeToGlossaryTerm = (source: TermNode): GlossaryTerm => ({
    title: source.frontmatter?.title as string,
    url: source?.fields?.slug ? `${InternalLinks.Glossary}${source.fields?.slug}` : "",
    id: source.id
})